.nav-color {
	background-image: -webkit-linear-gradient(#A39367, #877A52);
	background-image: linear-gradient(#A39367, #877A52);
}

.footer-color {
	background-image: -webkit-linear-gradient(#987FB2, #6C5293);
	background-image: linear-gradient(#987FB2, #6C5293);
	color: white;
}

#brand-pic {
	padding-left: 10px;
}

footer a {
	color: rgba(255,255,255,.75);
}

footer a:hover {
	color: #A39367;
}

.nav-link {
	margin-right: .3rem;
}

#playlistContainer {
	flex-direction: column;
}

.section-space {
	margin-top: 2rem;
}

main {
	margin: 2rem 0rem;
}

.centered {
	color: white;
	text-align: center;
	width: 80%;
	font-weight: bold;
	z-index: 99;
	position: absolute;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
}

@media (min-width: 575px) {
	
	.label-space {
		margin-right: 1rem;
	}

	.form-space {
		margin: 1rem 0;
	}

}


/*
for different sized browsers
*/
@media (min-width: 900px) {

	.nav-link {
		margin-right: 1rem;
	}

	#playlistContainer {
		flex-direction: row;
		flex-wrap: wrap;
	}

	#playerArea {
		width: 60%;
	}

}
